import React, { useState, useEffect } from 'react';
import { useHistory, useLocation, Link, Redirect } from 'react-router-dom';
import {
	Wrapper,
	Box,
	Input,
	Header,
	Button,
	ErrorInfo,
	Addition
} from './style';
import { message } from 'antd';
import { connect } from 'react-redux';
import * as actions from './store/actionCreators';

/* 
练习：登录组件
用到的知识：
Hook
axios
H5 Required
styled-components
Antd
Router跳转
 */

const Login = (props) => {
	//use Hooks
	const [username, setUsername] = useState('');
	const [password, setPassword] = useState('');
	const histroy = useHistory();

	useEffect(() => {
		if (props.error === true) {
			message.error("登录失败，请检查信息是否正确", 1);
			props.handleRecover();
		}
	}, [props.error])

	useEffect(()=> {
		if (props.login === true) {
			histroy.push('/'); //跳转到主界面
		}
	})

	if(props.login) {
		message.success("登陆成功", 1)
		return (
			<Redirect to='/'/>
		)
	} else {
		return (
			<Wrapper>
				<Box>
					<Header><span className="iconfont"><Link to='/'>&#xe601;</Link></span>登录</Header>
					{props.error ? <ErrorInfo>登陆失败，请检查</ErrorInfo> : null}
					<label htmlFor="username">用户名</label>
						<Input value={username} onChange={handleChange} id="username" name="username" type="text" required/>
					<label htmlFor="password">密码</label>
						<Input value={password} onChange={handleChange} id="password" name="password" type="password" required/>
					<Button type="submit" onClick={() => {props.handleSubmit(username, password)}}>登录</Button>
					<Addition>
						<span onClick={ handleForget }>忘记密码？</span>
						<span><Link to="/register">注册账户</Link></span>
					</Addition>
				</Box>
			</Wrapper>
		)
	}

	
	function handleForget() {
		alert("请与管理员联系")
	}

	function handleChange(e) {
		if(e.target.name === "username") {
			setUsername(e.target.value);
		} else {
			setPassword(e.target.value)
		}
	}

}

const mapState = (state) => ({
	login: state.getIn(["login","login"]),
	error: state.getIn(["login","error"])
})

const mapDispatch = (dispatch) => ({
	handleRecover() {
		dispatch(actions.getLogoutAction());
	},
	checkLoginState() {
		dispatch(actions.checkLoginStateAction());
	},
	handleSubmit(uname, pwd) {
		dispatch(actions.createLoginAction(uname,pwd))
	}
})

export default connect ( mapState, mapDispatch ) ( Login );
