import styled from 'styled-components';
import { Input as AntInput } from 'antd';

//登录界面样式
export const Wrapper = styled.div`
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #f0f2f5;
`;

export const Box = styled.div`
    height: 400px;
    position: relative;
    width: 300px;
    background: #fff;
    border-radius: 15px;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    label {
        font-size: 15px;
        width: 200px;
        text-align: left;
        margin-bottom: 5px;
    }
    .ant-input {
        width: 200px;
        height: 40px;
        margin-bottom: 20px;
    }
`;

export const Header = styled.h1`
    width: 200px;
    height: 40px;
    margin-bottom: 25px;
    font-size: 25px;
    line-height: 25px;
    font-weight: 450;
    color: #444;
    display: flex;
    align-items: center;
    .iconfont {
        cursor:pointer;
        display: block;
        margin-right: 10px;
        font-size: 30px;
    }
`

export const Input = styled(AntInput).attrs(props => ({
    placeholder: props.type === "text" ? "UserName" : "Password"
}))`
    font-size: 15px;
    transition-duration: 100ms;
`

export const Button = styled.button`
    height: 40px;
    width: 200px;
    font-size: 16px;
    border: none;
    background: #1E90FF;
    color: white;
    border-radius: 2px;
    cursor: pointer;
    transition: background 100ms ease-out;
    &:hover {
        background: #40a9ff
    }
    &:active {
        background: #096dd9;
    }
`

export const ErrorInfo = styled.div`
    position: absolute;
    top: 76px;
    width: 200px;
    height: 18px;
    font-size: 13px;
    line-height: 18px;
    color: #DC143C;
`;

export const Addition = styled.div`
    width: 200px;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    span {
        display: block;
        cursor: pointer;
        font-size: 13px;
    }
`;



